<template>
  <div class="portfolio">

    <v-layout row wrap>
      <v-flex xs12 class="main-title">
        <div class="svg-wrapper">
          <svg height="60" width="320" xmlns="http://www.w3.org/2000/svg">
            <rect class="shape" height="60" width="320" />
          </svg>
          <div class="text">PORTFOLIO</div>
        </div>
      </v-flex>
    </v-layout>
    <div class="icon-row">
      <img class="logo-icon" src="../assets/logos/html5.png">
      <img class="logo-icon" src="../assets/logos/css3.png">
      <img class="logo-icon" src="../assets/logos/javascript.png">
      <img class="logo-icon" src="../assets/logos/jQuery.png">
      <img class="logo-icon" src="../assets/logos/nodejs.png">
      <img class="logo-icon" src="../assets/logos/vue.png">
      <img class="logo-icon" src="../assets/logos/materialize.png">
      <img class="logo-icon" src="../assets/logos/vuetify.png">
    </div>

  </div>
</template>

<script>
  export default {
    name: 'portfolio',
    data() {
      return {

      }
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .logo-icon {
    height: 6vw;
    margin: 2vw;
  }

  .icon-row {
    display: flex;
    justify-content: center;
  }

  .main-title {
    margin-top: 20vh;
  }

  .main-subtitle {
    text-align: center;
    font-family: 'Cinzel';
    letter-spacing: 3px;
  }

  .svg-wrapper {
    text-align: center;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    margin: 0 auto;
    width: 320px;
    height: 70px;
  }

  .shape {
    stroke-dasharray: 140 540;
    stroke-dashoffset: -474;
    stroke-width: 8px;
    fill: transparent;
    stroke: #19f6e8;
    border-bottom: 5px solid black;
    transition: stroke-width 1s, stroke-dashoffset 1s, stroke-dasharray 1s;
  }

  .text {
    font-family: 'Cinzel';
    font-size: 22px;
    line-height: 32px;
    letter-spacing: 8px;
    color: #fff;
    top: -48px;
    position: relative;
  }

  .svg-wrapper:hover .shape {
    stroke-width: 2px;
    stroke-dashoffset: 0;
    stroke-dasharray: 760;
  }
</style>


